<template>
<div class="ickt-shopping-car">
    <div class="btn-price">共￥{{$store.getters.price}}元</div>
    <!-- 使用js的跳转方式是为了做跳转拦截 -->
    <div class="btn-buy" @touchend="showBuyPage">选好了</div>
    <!-- <router-link to="/buy" class="btn-buy">选好了</router-link> -->
</div>
</template>
<style lang="scss">
.ickt-shopping-car {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    height: 50px;
    line-height: 50px;
    background-color: #484d54;
    display: flex;
    color: #fff;
    font-size: 20px;
    text-align: center;
    .btn-price {
        flex: 1;
    }
    .btn-buy {
        width: 120px;
        background-color: #fe2947;
    }
}
</style>
<script>
export default {
    // 方法
    methods: {
        // 进入购买页面
        showBuyPage() {
            // 条件：有商品加入了购物车
            if (this.$store.state.products.length) {
                // 可以进入购买页面
                return this.$router.push('/buy')
                // return this.$router.replace('/buy')
            }
            // 提示错误
            alert('请购买商品！')
        }
    }
}
</script>