<template>
<div class="page-detail" v-show="data.price">
    <!-- 图片模块 -->
    <div class="image-part">
        <img :src="data.img" alt="">
        <h1>{{data.title}}</h1>
        <p>{{data.description}}</p>
    </div>
    <!-- 价格模块 -->
    <div class="price-part">
        <span class="price"><strong>{{data.price}}</strong>元</span>
        <span class="origin-price">门市价:{{data.originPrice}}元</span>
        <span :class="{
            'btn-buy': true,
            'has-buy': data.hasBuy
        }" @touchend="buyItem">{{data.hasBuy ? '取消购买' : '立即购买'}}</span>
    </div>
    <!-- {{$store.state.products}} -->
    <!-- 销售模块 -->
    <ul class="sales-part">
        <li>支持立即退货</li>
        <li>支持随时退货</li>
        <li>销量:{{data.sales}}</li>
    </ul>
    <!-- 店家模块 -->
    <div class="store-part module">
        <div class="module-header">店家信息</div>
        <div class="module-body">
            <p>{{data.storeName}}</p>
            <p>{{data.storeAddress}}</p>
        </div>
        <div class="module-footer">查看其它{{data.storeNum}}家分店</div>
    </div>
    <!-- 购买模块 -->
    <div class="notice-part module">
        <div class="module-header">购买须知</div>
        <div class="module-body" v-html="data.content"></div>
    </div>
    <!-- 购物车组件 -->
    <ShoppingCar></ShoppingCar>
    <!-- <shopping-car></shopping-car> -->
</div>
</template>
<style lang="scss">
@import '@/base.scss';
.page-detail {
    padding-bottom: 70px;
    .image-part {
        position: relative;
        img {
            width: 100%;
            display: block;
        }
        h1, p {
            position: absolute;
            bottom: 35px;
            left: 10px;
            color: #fff;
        }
        h1 {
            font-size: 24px;
            // font-weight: normal;
        }
        p {
            bottom: 10px;
            font-size: 18px;
        }
    }
    .price-part {
        background-color: #fff;
        border-bottom: 1px solid #ccc;
        padding: 0 10px;
        height: 50px;
        line-height: 50px;
        font-size: 16px;
        .price {
            color: $color;
            margin-right: 8px;
            strong {
                font-size: 24px;
                font-weight: normal;
            }
        }
        .btn-buy {
            float: right;
            padding: 5px 12px;
            color: #fff;
            background-color: #f30;
            font-size: 12px;
            line-height: 1;
            border-radius: 4px;
            margin-top: 14px;
            &.has-buy {
                color: #333;
                background-color: #ddd;
            }
        }
    }
    .sales-part {
        display: flex;
        flex-wrap: wrap;
        background-color: #fff;
        padding: 10px 0;
        li {
            width: 50%;
            color: yellowgreen;
            padding-left: 10px;
            line-height: 26px;
            box-sizing: border-box;
            &:last-child {
                color: #000;
            }
        }
    }
    .module {
        margin-top: 10px;
        background-color: #fff;
        padding: 10px;
        .module-header {
            border-bottom: 1px solid #ccc;
            padding: 8px 0 10px;
            font-size: 20px;
        }
        .module-body {
            padding: 10px 0;
            font-size: 16px;
            line-height: 28px;
            h1, h2, h3, h4, h5, h6 {
                font-weight: normal;
                font-size: 18px;
                color: #f30;
            }
        }
        .module-footer {
            border-top: 1px solid #ccc;
            padding: 10px 0 8px;
            font-size: 14px;
            color: skyblue;
        }
    }
}
</style>
<script>
// 引入购物车组件
import ShoppingCar from '@/components/ShoppingCar';
export default {
    // 注册组件
    components: { ShoppingCar },
    // 数据
    data() {
        return {
            data: {}
        }
    },
    // 方法
    methods: {
        // 购买商品
        buyItem() {
            // 提交数据
            this.$store.commit(this.data.hasBuy ? 'deleteItem' : 'addItem', this.data)
            // hasBuy表示是否购买
            // this.data.hasBuy = !this.data.hasBuy
            // 避免数据丢失
            this.$set(this.data, 'hasBuy', !this.data.hasBuy)
            // console.log(this.data.hasBuy, this.data)
            // 提交数据
            // this.$store.commit(this.data.hasBuy ? 'addItem' : 'deleteItem', this.data)
        },
        // 获取数据
        getData() {
            // 解构路由数据
            let { params } = this.$route;
            // 获取数据
            this.$http 
                .get('/data/detail', { params })
                // 监听数据返回
                .then(({ data }) => {
                    // 判断数据是否存在
                    if (data) {
                        // 如果数据在购物车中，设置为购买的状态
                        if (this.$store.state.products.find(item => item._id === data._id)) {
                            // 设置为购买状态
                            data.hasBuy = true;
                        }
                        // 存储数据
                        this.data = data;
                    } else {
                        // 返回上一个页面
                        this.$router.go(-1);
                    }
                })
        }
    },
    // 创建完成
    created() {
        // 获取数据
        this.getData()
    },
    // 监听器
    watch: {
        // 路由改变
        $route() {
            // 获取数据
            this.getData()
        }
    }
}
</script>